import * as React from 'react';
import { graphql } from 'gatsby';

import Markdown from 'react-markdown';
import rehypeRaw from 'rehype-raw';
import Moment from 'react-moment';
import CarouselZoom from './carouselZoom';
import SEO from './seo';

import classNames from 'classnames/bind';
import * as styles from './news.module.scss';

const cx = classNames.bind(styles);

type Props = {
  data: GatsbyTypes.NewsItemQuery;
};

const News: React.FC<Props> = ({
  data: {
    mpapi: {
      newsItems: { data: newsItems },
    },
  },
}) => {
  if (!newsItems) return null;

  const newsItem = newsItems[0].attributes;

  return (
    <>
      <SEO
        title={`${newsItem.title} | Atelier Mendgen Peissl`}
        description={newsItem.content.substring(0, 150)}
        image={newsItem.images.data[0].attributes.formats?.thumbnail?.url}
      />
      <div className={cx('newsWrapper')}>
        <section className={cx('newsContent')}>
          <CarouselZoom images={newsItem.images.data} />
          <h1 className={cx('title')}>{newsItem.title}</h1>
          <p className={cx('small')}>
            <Moment format="MMMM Do YYYY">{newsItem.startDate}</Moment>
            {newsItem.endDate && (
              <span>
                {' '}
                until <Moment format="MMMM Do YYYY">{newsItem.endDate}</Moment>
              </span>
            )}
          </p>
          {newsItem.content && (
            <Markdown rehypePlugins={[rehypeRaw]}>{newsItem.content}</Markdown>
          )}
        </section>
      </div>
    </>
  );
};

export const query = graphql`
  query NewsItem($slug: String!) {
    mpapi {
      newsItems(filters: { slug: { eq: $slug } }) {
        data {
          attributes {
            slug
            title
            content
            images {
              data {
                attributes {
                  alternativeText
                  height
                  width
                  url
                  formats
                  hash
                }
              }
            }
            startDate
            endDate
          }
        }
      }
    }
  }
`;

export default News;
